        width: 350px;
        z-index: -1;
        border: 3.5px solid #171c46;
    }

    .profile-image-blank {
        display: flex;
    }
    .row-gif {
        /*display: flex;
        height: 50%;
        width: 100%;
        z-index: 2;*/
