        opacity: 0.8;
        color: #fff;;
        font-size: 1.3rem;
        line-height: 1.3rem;
        text-decoration: none;
    }

    .nav-link:visited {
        opacity: 1;
    }

    .nav-link:hover {
        padding-bottom: 2px;
        border-bottom: 1px solid #fff;
        opacity: 1;
        transition: all 0.25;
    }
    .image-prof-about {
        width: 100%; 
    }
    .image-prof-a {
        height: 190px;
    }
    .set-up-profile-about {
        left: 14%;
        top: -15%;
        opacity: 0;
    }
    .animate-from-a {
        top: 0%;
        left: 0%;
        opacity: 1;
        transition: all 1.2s ease-in;
    }

    .slide-down-about {
        animation: slide-down 2.6s forwards .5s 1;
        opacity: 1;
    }
    .p-over {
        width: 450px;
    }
}

