@media screen and (max-width: 1199px) {
    .wrapper {
        width: 100%;
        height: 100%;
    }
    .wrapper-main {
        display: flex;
        width: 100%;
        flex-direction: column;
        z-index: 0;
        height: 100%;
    } 
    .left-container-about {
        background-color: #F4C42F;
        height: 70px;
    }

    .text-container {
        width: 100%;
        font-size: 1em;
        color: white;
    }
    .text-container-about {
        display: flex;
        flex-direction: column;
        align-items: center;
        opacity: 0;
        transform: translateY(-10%);
        transition: opacity 3s ease-in;
        transition-delay: 1.8s;
    }

    .animate-profile {
        transform-style: preserve-3d;
        transition: all 1600ms ease 1s;
        opacity: 0;
        transform: translateX(0px) translateY(-100px) translateZ(0px);
    }

    .on-load-profile {
        transition: all 1800ms ease 1s;
        transform: translateX(0px) translateY(0px) translateZ(0px);
        opacity: 1;
    }

    .about-text {
        font-size: 1.3em;
        margin: 5px 20px;
        opacity: 0.9;
         margin-bottom: -10px;
        // position: absolute;
        // top: 80px;
        // left: 8%;
    }
    .text-syle {
        font-size: medium;
        // letter-spacing: 0.015em;
        // word-spacing: 0.4em;
        color: #F4C42F;
    }
    .scrollable-container {
        opacity: 0.9;
        display: flex;
        flex-direction: column;
        position: relative;
        bottom: 20px;
    }

    .main-text {
        color: white;
        margin-top: 15px;  
    }
    .emp-text {
        color: #F4C42F;
        font-size: 4.25em;
        margin: 20px 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .p-over {
        overflow-wrap: break-word;
        word-wrap: break-word;
        hyphens: auto;
       /* padding: 0 20px;*/
        width: 75%;
        margin: auto;
    }
    .text-paragraph {
        margin: 4px 0;
    }
    .my-name-about {
        font-size: 1.5em;
        font-weight: 600;
        margin: 12px 0 0 20px;
        height: 70px;
    }
    
    .name-mayer {
        border-bottom: solid 7px #171C46;
    }
    .right-container-about {
        display: flex;
        flex-direction: column;
        justify-items: left;
        align-items: center;
        height: 100%;
        background-color: #171C46;

    }

    .main-content {
        display: flex;
        flex-direction: column;
        background-color: #171C46;

    }
    .main-empty-about {
        height: 420px;
    }

    .floating-profile-a {
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        top: 120px;
        left: 0;
        right: 0;
        /*height: 50%;*/
        z-index: 2
    }
    
    .image-prof-about {
        width: 100%; 
    }

    .large-text {
        color:  white;
        font-size: 5em;
        margin: 20px 0;
    }

    .navigation-about {
        left: -100%;
        display: flex;
        flex-direction: column;
        position: absolute;
        background-color: #F4C42F;
        width: 100%;
        font-size: 3em;
        height: 100vh;
        z-index: 3;
        transition: left .5s;
        justify-content: center;

        .nav-link {
           font-size: 1.2em;
           color: #171C46;
            &:hover {
                color: #fff;
                opacity: 0.7;
            }
       }
    }
    .navigation-about p {
        margin: 10px 0 10px 0
    }

    .navigation-about.is-nav-open {
        left: 0;
        padding-left: 20px;

    }
    .nav-icon {
        margin: 1em;
        width: 40px;
        position: absolute;
        right: 5px;
        top: 0;
        bottom: 0;
      }
      
      .nav-icon div {
        background-color: #171C46;
        border-radius: 3px;
        content: '';
        display: block;
        height: 3px;
        margin: 7px 0;
        transition: all .2s ease-in-out;
      }

      .nav-icon.is-nav-open:before {
        transform: translateY(10px) rotate(135deg);
      }
      
      .nav-icon.is-nav-open:after {
        transform: translateY(-10px) rotate(-135deg);
      }
      
      .nav-icon.is-nav-open div {
        transform: scale(0);
      }
      .link-text {
          color: #D26442;
          text-decoration: none;
      }
      .nav-link:link {
        text-decoration: none;
        color: #171C46;
      }
      .about-details {
          display: flex;
          flex-direction: column;
          align-items: center;
      }

      .slide-down-about {
        animation: slide-down 2.6s forwards 1.5s 1;
        opacity: 1;
    }
    .p-over {
        margin-bottom: 15px !important;
    }
    &.links {
        a {
            margin-bottom: 15px;
            color: #F4C42F;
            display: block;
            font-size: 1.3em;
            text-decoration: none;
            padding-bottom: 2px;
            border-bottom:solid 1.5px transparent;
            &:hover {
                border-bottom: 1.5px solid #F4C42F;
            }
        }
    }
}

.nav-link {
    color: #fff;
    opacity: 0.8;
    font-size: 1.3rem;
    line-height: 1.3rem;
    text-decoration: none;
    &.active {
        opacity: 0.8 !important;
    }
}

.p-over {
    .p-link {
        display: inline-block;
        color: #f4c430;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    .item {
        font-weight: 550;
        margin-bottom: 5px;
        color: #d26442;
    }
    .value {
        display: block;
        margin-bottom: 10px;
        margin-top: 5px;
    }
    &.links {
        a {
            margin-bottom: 15px;
            color: #F4C42F;
            display: block;
            font-size: 1.3em;
            text-decoration: none;
            padding-bottom: 2px;
            border-bottom:solid 1.5px transparent;
            &:hover {
                border-bottom: 1.5px solid #F4C42F;
            }
        }
    }
}

